import React, {useEffect, useState} from "react";
import {MainContainer} from "./components/MainContainer";
import axios from "axios";
import Typography from "@material-ui/core/Typography";
import {MuiThemeProvider, TableContainer} from "@material-ui/core";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import TableBody from "@material-ui/core/TableBody";
import makeStyles from "@material-ui/core/styles/makeStyles";
import {useHistory} from "react-router-dom";
import {Form} from "./components/Form";
import {PrimaryButton} from "./components/PrimaryButton";


const useStyles = makeStyles((theme) => ({
    root: {
        marginBottom: "30px",
        textAlign: "center",
        width: 'auto',
        minWidth: 0,
    },
    table: {
        marginBottom: "30px"
    },
    head: {
        backgroundColor: "orange",
        color: 'white'
    },
}))

export const Main = () => {
    const history = useHistory()
    const [data, setData] = useState([])
    const myStyle = useStyles()

    const onSubmit = () => {
        history.push('/step1')
    }

    const res = useEffect(() => {
        axios({
            method: "GET",
            url: "http://127.0.0.1:8000/api/card/"
        }).then(response => {
            console.log('check response')
            console.log(response)
            console.log(response.data)
            setData(response.data ?? [])
            // setData(response.data.results ?? [])
        })
    }, [])
    return (
        <MainContainer>
            <Typography component={'h2'} variant={'h5'}>
                All user cards ü•≥üéÉ
            </Typography>
            {/*<ul>*/}
            {/*    { data && data.map(p => (*/}
            {/*        <li key={p.id}>{p.firstName}--{p.email}</li>*/}
            {/*    ))}*/}
            {/*</ul>*/}
            <MuiThemeProvider className={myStyle.root}>
                <TableContainer className={myStyle.root} component={"div"}>
                    <Table className={myStyle.table}>
                        <TableHead>
                            <TableRow>
                                <TableCell>
                                    Name
                                </TableCell>
                                <TableCell align={'right'}>
                                    Last Name
                                </TableCell>
                                <TableCell>
                                    E-mail
                                </TableCell>
                                <TableCell>
                                    Phone
                                </TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {data.map(p => (
                                <TableRow key={p.id}>
                                    <TableCell>{p.firstName}</TableCell>
                                    <TableCell>{p.lastName}</TableCell>
                                    <TableCell><a href={`mailto:${p.email}`}>{p.email}</a></TableCell>
                                    {/*<TableCell align={'right'}>{p.phoneNumber?.toString()}</TableCell>*/}
                                    <TableCell align={'right'}><a href={`tel:{p.phoneNumber?.toString()}`} className="Phonny">
                                        {p.phoneNumber?.toString()}
                                    </a></TableCell>
                                    <TableCell><img src={p.files} style="border: 0; height: 31px; width: 88px;" alt="–ù–∞–ø—Ä–∏–º–µ—Ä"/></TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </TableContainer>
            </MuiThemeProvider>
            <Form onClick={onSubmit}>
                <PrimaryButton>Add user card</PrimaryButton>
            </Form>
        </MainContainer>
    );
}